.code-drop{
    position: absolute;
    left:0;
    margin-left: -25px;
    opacity: 0;
    visibility: hidden;
    transform: scale(0);
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    -ms-transform: scale(0);
    transform-origin: 50% 0;
    -webkit-transform-origin: 50% 0;
    -moz-transform-origin: 50% 0;
    -ms-transform-origin: 50% 0;
    transition: transform .35s,opacity .35s,visibility .35s;
    -webkit-transition: transform .35s,opacity .35s,visibility .35s;
    -moz-transition: transform .35s,opacity .35s,visibility .35s;
    -ms-transition: transform .35s,opacity .35s,visibility .35s;
    z-index: 10;
    border-radius: 5px;
    background-color: #fff;
    padding:6px;
    margin-top: 10px;
    &:before{
        content:"";
        position:absolute;
        width:0;
        height:0;
        top:-8px;
        left:50%;
        margin-left: -8px;
        border-bottom: 8px solid #fff;
        border-right: 8px solid transparent;
        border-left: 8px solid transparent;
    }
    img{width:84px;}
}
.wechat-hov{
    position:relative;
    &:hover{
        .code-drop{
            opacity: 1;
            visibility: visible;
            transform: scale(1);
            -webkit-transform: scale(1);
            -moz-transform: scale(1);
            -ms-transform: scale(1);
        }
    }
}
