#GettingHereFSBuses_content{
    .container{
        .row{
            margin-top: 20px;
            @media (max-width: $screen-md-max){
                margin-top: 0px; }
            div{
                .row{
                    @media (max-width: $screen-md-max){
                margin-top: 10px; }
                    div{
                        font-size: 15px;
                        color: #000;
                    }
                    .get_c_common{
                        height: 92px;
                        
                    }
                    .get_content_left{
                        text-align: center;
                        line-height: 34px;
                        font-size: 24px;
                        color: #343434;
                        font-weight: normal;
                        background: #e6e6e6;
                        padding: 16px 24px 0;
                        overflow: hidden;
                        @media (max-width: $screen-md-max){
                            font-size: 20px;
                            line-height: 30px;
                            padding: 0px;
                            height: 34px;
                        }
                    }
                    .get_content_right{
                        padding-left: 2px;
                        @media (max-width: $screen-md-max){
                            padding-left: 0px;
                            height: 76px;
                        }
                        p{
                            
                            height: 30px;
                            margin: 0px;
                            padding-left: 30px;
                            line-height: 30px;
                            font-size: 15px;
                            color: #000;
                            padding-right: 34px;
                            
                            @media (max-width: $screen-md-max){
                                height: 25px;
                                padding-right: 23px;
                                font-size: 12px;
                                line-height: 23px;
                            }
                           @media screen and(min-width:1200px)and(max-width:1240px){
                                padding-right: 0px;
                            }
                            .c_left{
                                float: left;
                                color: #000;
                                font-size: 15px;
                                @media (max-width: $screen-md-max){
                                    font-size: 12px;
                                }
                            }
                            .c_right{
                                float: right;
                                color: #000;
                                font-size: 15px;
                                @media (max-width: $screen-md-max){
                                    font-size: 12px;
                                }
                            }
                        }
                        p:nth-child(1){
                            background: #c0c0c0;
                        }
                        p:nth-child(2){
                            background: #d0cfcf;
                            margin-top: 1px;
                            margin-bottom: 1px;
                        }
                        p:nth-child(3){
                            background: #e1e1e1;
                        }
                    }
                }
            }
            .get_padd_right{
                padding-right: 5px;
                @media (max-width: $screen-md-max){
                    padding-right: 0px;
                }
            }
            .get_padd_left{
                padding-left: 5px;
                @media (max-width: $screen-md-max){
                    padding-left: 0px;
                    // margin-top: 33px;
                }
            }
        }
    }
    @media (max-width: $screen-md-max){
        margin-top: 30px;
        padding: 0 12px;
        .container{
            .row{
                // margin-top: 0px;
                div{
                    font-size: 20px;
                }
            }
        }
    }
}
.getting_Parking{
    display: none;
}